$image-url: url("./assets/img-1.jpg");

$color-primary: #143d42;
$color-secondary: #000000;
$color-green-light: #5ce6aa;

$color-grey: #f6f6f6;
$color-grey-dark: #000000;

$linear-gradient-1: linear-gradient(
  to right,
  #5ce6aa,
  #75eda3,
  #8df39c,
  #a4f995,
  #bcff8f
);

$linear-gradient-2: linear-gradient(
    to right,
    rgba(92, 230, 170, 0.8),
    rgba(117, 237, 163, 0.8),
    rgba(141, 243, 156, 0.8),
    rgba(164, 249, 149, 0.8),
    rgba(188, 255, 143, 0.8)
  ),
  url("./assets/img-1.jpg") no-repeat top center fixed;
